header.header {
  .line:first-child {
    margin-top: 0; }
  .line {
    display: block;
    width: 38px;
    height: 3px;
    background: #fff;
    transition: opacity 0.3s cubic-bezier(0.645, 0.045, 0.355, 1) 0s, transform 0.24s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
    margin-top: 7px; }
  .nav-trigger {
    position: fixed;
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 50%;
    background-color: rgba(#000000,0.5);
    right: 5px;
    top: 30px;
    cursor: pointer;
    z-index: 200;
    transform-origin: 110px 0px;
    transform: scale(0.6);
    transition: background 0.3s linear;
    @include r($wide) {
      right: 40px; }
    @include r($mobile) {
      top: 15px; }
    &.close {
      background: transparent; } }
  .close .line {
    &:nth-child(1) {
      transform: translate(0px, 8px) rotate(45deg); }
    &:nth-child(2) {
      opacity: 0; }
    &:nth-child(3) {
      transform: translate(0px,-12px) rotate(-45deg); } }
  nav {
    max-width: 451px;
    width: 100%;
    position: fixed;
    right: -20px;
    top: 20px;
    z-index: 100;
    @include r($tablet) {
      right: 0px;
      max-width: 310px; }
    @include r($mobile) {
      max-width: 220px;
      top: -20px; }
    &.out {
      pointer-events: none; }
    &.in {
      pointer-events: auto; }
    ul {
      display: block;
      padding: 0;
      margin: 0;
      width: 100%;
      cursor: pointer; } }
  nav {
    ul {
      height: 100%;
      width: 100%;
      position: absolute; }
    li:first-of-type {
      margin-top: 0;
      padding-top: 80px;
      @include r($tablet) {
        padding-top: 50px; }
      @include r($mobile) {
        padding-top: 60px; } }
    li:last-of-type {
      padding-bottom: 50px;
      @include r($tablet) {
        padding-bottom: 30px; } }
    li {
      position: relative;
      display: block;
      width: 100%;
      cursor: pointer;
      padding: 35px 0;
      background-color: #1e1e1e;
      @include r($desktop) {
        padding: 25px 0; }
      @include r($tablet) {
        padding: 20px 0; }
      @include r($mobile) {
        padding: 13px 0; }
      a {
        color: #ffffff;
        display: block;
        text-align: center;
        font-size: 25px;
        font-weight: 700;
        font-family: "FuturaPT";
        letter-spacing: 1.25px;
        @include r($tablet) {
          font-size: 20px; }
        @include r($mobile) {
          font-size: 16px;
          font-weight: 500; } }
      &:hover {
        a {
          color: #02fbf7; } } }
    &.in li {
      &:nth-child(1) {
        transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) 0.1s, opacity 0.5s linear 0.1s; }
      &:nth-child(2) {
        transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) 0.2s, opacity 0.5s linear 0.2s; }
      &:nth-child(3) {
        transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) 0.3s, opacity 0.5s linear 0.3s; }
      &:nth-child(4) {
        transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) 0.4s, opacity 0.5s linear 0.4s; }
      &:nth-child(5) {
        transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) 0.5s, opacity 0.5s linear 0.5s; } }
    &.out li {
      &:nth-child(5) {
        transition: transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0s, opacity 0.3s linear 0s; }
      &:nth-child(4) {
        transition: transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0.05s, opacity 0.3s linear 0.05s; }
      &:nth-child(3) {
        transition: transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0.1s, opacity 0.3s linear 0.1s; }
      &:nth-child(2) {
        transition: transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0.15s, opacity 0.3s linear 0.15s; }
      &:nth-child(1) {
        transition: transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0.2s, opacity 0.3s linear 0.2s; }
      transform: translateY(300%);
      opacity: 0; }
    &.in li {
      transform: translateY(0);
      opacity: 1; } }
  .nav-label {
    position: absolute;
    width: 100%;
    left: 0;
    text-align: center;
    height: 16px;
    top: 50%;
    margin-top: -8px;
    text-transform: uppercase; } }
