// Styles for dialog window
.mfp-wrap {
  -webkit-overflow-scrolling: touch;
  -webkit-transform: translateZ(0); }

html.mfp-helper {
  height: 100%;
  body {
    overflow: hidden;
    height: 100%;
    -webkit-transform: translateZ(0); } }

#small-dialog {
  background: white;
  padding: 20px 30px;
  text-align: left;
  max-width: 600px;
  margin: 40px auto;
  position: relative; }
.mfp-close-btn-in .mfp-close {
  color: transparent;
  right: -58px;
  top: -58px;
  width: 22px;
  height: 22px;
  background: url(../img/x.png) no-repeat center;
  background-size: 100% 100%;
  @include r($tablet) {
    right: 4px; }
  @include r($mobile) {
    top: 12px;
    filter: brightness(0.1);
    right: 11px; } }
.mfp-image-holder .mfp-close,
.mfp-iframe-holder .mfp-close {
  width: inherit;
  min-width: 33px; }
.white-popup-block {
  max-width: 544px;
  width: 100%;
  padding-top: 26px;
  text-align: left;
  margin: auto;
  position: relative;
  background-color: #ffffff;
  @include r($wide) {
    padding-bottom: 26px; }
  @include r($mobile) {
    padding: 25px; }
  h2 {
    font-size: 40px;
    color: #40434c;
    margin-bottom: 8px;
    @include r($mobile) {
      font-size: 25px; }
    &::after {
      display: none; } }
  p.politic {
    margin-top: 20px;
    text-align: center;
    margin-bottom: 10px; }
  p.politic, a {
    color: #808080;
    font-family: "ProximaNova";
    font-size: 16px;
    font-weight: 300;
    line-height: 25px;
    letter-spacing: 0.8px; }
  a {
    text-decoration: underline; }
  form {
    display: flex;
    flex-direction: column;
    padding: 0 45px;
    @include r($mobile) {
      padding: 0; } }
  .ui-group {
    width: 100%; }
  .ui-group.submit {
    max-width: 375px; }
  input[type="submit"] {
    border: none; }
  .wrapper-img {
    @include r($wide) {
      display: none; } }
  img {
    display: block;
    width: 100%;
    height: auto; } }
.popup-buble {
  .popup-dialog {
    opacity: 0;
    transition: all 0.2s ease-in-out;
    transform: scale(0.8); }
  &.mfp-ready .popup-dialog {
    opacity: 1;
    transform: scale(1); }
  &.mfp-removing .popup-dialog {
    transform: scale(0.8);
    opacity: 0; }
  &.mfp-bg {
    background: rgba(0, 0, 0, 0.6);
    opacity: 0;
    transition: opacity 0.3s $ease; }
  &.mfp-ready.mfp-bg {
    opacity: 1; }
  &.mfp-removing.mfp-bg {
    opacity: 0; } }
#modal-form-tnx {
  padding-bottom: 20px;
  .popup-dialog {}
  p {
    font-size: 20px;
    text-align: center; } }
#modal-form {
  .wrapper-img {
    padding-bottom: 20px;
    padding-top: 20px;
    margin-left: auto;
    margin-right: auto;
    width: calc( 100% - 30px ); } }
