.up_btn {
  position: fixed;
  border-radius: 50%;
  background-color: #e9e5d2;
  border: 1px solid #504001;
  right: 20px;
  bottom: 20px;
  cursor: pointer;
  width: 40px;
  height: 40px;
  z-index: 10;
  transition: 0.3s;
  display: none;
  &::after {
    position: absolute;
    content: '';
    display: block;
    top: 16px;
    left: 12px;
    width: 12px;
    height: 12px;
    border-left: 2px solid #000;
    border-bottom: 2px solid #000;
    transform: rotate(135deg); } }
button {
  border: 0;
  box-shadow: none;
  background: none;
  cursor: pointer;
  &:focus {
    outline: none; } }
.btn {
  margin-left: auto;
  margin-right: auto;
  padding: 16px 0;
  text-align: center;
  max-width: 375px;
  width: 100%;
  display: block;
  color: #40434c;
  font-family: "FuturaPT";
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 1.08px;
  border-radius: 25px;
  background-image: linear-gradient(-45deg, #00fffb 0%, #3effd2 100%);
  &:hover {
    background: #1be9e5; } }
