* {
  box-sizing: border-box;
  outline: none; }

body, html {
  background: white;
  color: $fontColor;
  font-family: $font-family;
  font-size: 16px;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.page {
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  &__content {} }
.container {
  max-width: 1180px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @include r($wide) {
    max-width: 970px; }
  @include r($desktop) {
    max-width: 750px; }
  @include r($tablet) {
    padding-left: 15px;
    padding-right: 15px; } }
h1,h2,h3,h4,h5,h6,p,a,span,ul,ol,li {
  padding: 0;
  margin: 0; }
h2 {
  color: #ffffff;
  font-family: "FuturaPT";
  font-size: 47px;
  font-weight: 700;
  letter-spacing: 2.5px;
  text-align: center;
  position: relative;
  margin-bottom: 60px;
  &::after {
    position: absolute;
    left: 50%;
    bottom: -30px;
    transform: translateX(-50%);
    content: '';
    display: block;
    width: 226px;
    height: 16px;
    background: url(../img/decor-h2.png) no-repeat center; }
  span {
    color: #18e9e5; } }
p {
  color: #40434c; }
a {
  color: $fontColor;
  text-decoration: none;
  font-family: $font-family;
  font-size: 18px;
  font-weight: 400;
  &:hover, &:focus {
    text-decoration: none; } }
ul {
  list-style: none;
  li {} }
ol {
  list-style: decimal; }
.map-label {
  width: 1px;
  height: 1px;
  position: relative;
  &::before {
    position: absolute;
    left: 0;
    transform: translateX(-50%);
    bottom: 100%;
    content: '';
    display: block;
    width: 47px;
    height: 63px;
    background: url(../img/marker-2.svg) no-repeat center; }
  &::after {
    bottom: 18px;
    left: 30px;
    width: 90px;
    position: absolute;
    content: 'Геолог Ямала ★ 4.0';
    display: block;
    background: rgba(255,255,255,.9);
    padding: 3px 6px 4px;
    border-radius: 6px;
    line-height: inherit;
    height: auto;
    font-size: 14px;
    line-height: 18px;
    font-weight: 400;
    color: #000; } }
.wrapper-slider {
  position: relative; }
.swiper-button-next, .swiper-button-prev {
  transform: none;
  margin: 0;
  padding: 0;
  top: auto;
  bottom: auto;
  left: auto;
  right: auto;
  background: none;
  color: transparent;
  width: 46px;
  height: 19px;
  svg {
    width: 46px;
    height: 19px;
    fill: #40434c; } }
