.wrapper-social-fixed {
    position: fixed;
    z-index: 26;
    top: 50%;
    transform: translateY(-50%);
    right: 110px;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include r($wide) {
        right: 50px; }
    @include r($tablet) {
        right: 25px; }
    @include r($mobile) {
        right: 10px; }
    .soc-item:last-child {
        margin-bottom: 0; }
    .soc-item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 49px;
        height: 49px;
        border-radius: 50%;
        background-color: #ffffff;
        margin-bottom: 30px;
        @include r($mobile) {
            width: 35px;
            height: 35px;
            margin-bottom: 20px; } }
    .soc-item:nth-child(1) {
        svg {
            margin-right: 2px; } }
    svg {
        width: 24px;
        height: 24px;
        fill: #c8c8c8;
        transition: 0.3s;
        @include r($mobile) {
            width: 20px;
            height: 20px; }
        &:hover {
            fill: #00ccc9; } } }
footer.footer {
    background-color: #f1f5f9;
    padding-bottom: 40px;
    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include r($tablet) {
            flex-direction: column;
            align-items: center; } }
    .wrapper-logo {
        max-width: 168px;
        width: 100%;
        img {
            width: 100%;
            height: auto; } }
    .wrapper-copy-desc {
        @include r($tablet) {
            margin: 15px 0; }
        p:last-child {
            margin-bottom: 0; }
        p {
            color: #40434c;
            font-family: "ProximaNova";
            font-size: 18px;
            font-weight: 300;
            line-height: 25px;
            letter-spacing: 0.9px;
            text-align: center;
            margin-bottom: 10px;
            span {
                text-transform: uppercase; } } }
    .wrapper-social {
        display: flex;
        margin-bottom: 20px; }
    .soc-item:first-child {
        margin-left: 0; }
    .soc-item {
        width: 40px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
        border: 1px solid #ffffff;
        background-color: #ffffff;
        border-radius: 50%;
        margin-left: 20px;
        &:hover svg {
            fill: #00ccc9; } }
    svg {
        fill: #000000;
        width: 20px;
        height: 20px;
        transition: 0.3s; }
    .tel {
        display: block;
        text-align: center;
        color: #40434c;
        font-family: "ProximaNova";
        font-size: 20px;
        font-weight: 700;
        line-height: 25px;
        text-transform: uppercase;
        letter-spacing: 1px; }
    .info {
        margin-top: 30px;
        margin-left: auto;
        margin-right: auto;
        max-width: 825px;
        width: 100%;
        text-align: center;
        color: #40434c;
        font-family: "ProximaNova";
        font-size: 15px;
        font-weight: 300;
        line-height: 25px;
        letter-spacing: 0.75px; } }

