input,select {}
input[type="text"],
input[type="tel"],
input[type="email"],
select {
  border-radius: 4px;
  border: 1px solid #e1e1e1;
  background-color: #ffffff;
  color: #232323;
  font-family: $font-family;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 0.8px;
  padding: 10px 20px;
  -webkit-appearance: none;
  &::-webkit-input-placeholder {
    color: #818799;
    font-family: "ProximaNova";
    font-size: 18px;
    font-weight: 300;
    letter-spacing: 0.9px; }
  &::-moz-placeholder {
    color: #818799;
    font-family: "ProximaNova";
    font-size: 18px;
    font-weight: 300;
    letter-spacing: 0.9px; }
  &:-moz-placeholder {
    color: #818799;
    font-family: "ProximaNova";
    font-size: 18px;
    font-weight: 300;
    letter-spacing: 0.9px; }
  &:-ms-input-placeholder {
    color: #818799;
    font-family: "ProximaNova";
    font-size: 18px;
    font-weight: 300;
    letter-spacing: 0.9px; } }
input[type="submit"] {
  cursor: pointer; }
textarea {
  height: 120px;
  border-radius: 4px;
  border: 1px solid #e1e1e1;
  background-color: #ffffff;
  color: #232323;
  font-family: $font-family;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 0.8px;
  padding: 10px 20px;
  resize: none; }
.ui-group {
  position: relative;
  margin-bottom: 20px;
  input, textarea {
    width: 100%; } }
input[type="text"],
input[type="tel"],
input[type="email"], {
  border-radius: 25px;
  background-color: #ffffff;
  height: 50px; }


