img.svg {
    display: none; }
header.header {
    position: absolute;
    top: 12px;
    right: 0;
    left: 0;
    z-index: 27;
    .wrapper-logo {
        display: flex;
        align-items: center;
        @include r($mobile) {
            flex-direction: column; } }
    img {
        max-width: none;
        width: 175px;
        height: auto;
        margin-right: 25px;
        @include r($tablet) {
            width: 130px; }
        @include r($mobile) {
            width: 150px;
            margin-right: 0px;
            margin-bottom: 10px; } }
    h2 {
        color: #000000;
        font-family: "FuturaPT";
        font-size: 30px;
        font-weight: 500;
        letter-spacing: 1.8px;
        margin-bottom: 0;
        @include r($mobile) {
            font-size: 26px; }
        &::after {
            display: none; } }
    .container-no-fixed {
        position: absolute;
        left: 50%;
        transform: translateX(-50%); }
    .container-fixed {
        position: fixed;
        left: 50%;
        transform: translateX(-50%);
        z-index: 10; } }
